import { createAsyncThunk, createSlice } from '@reduxjs/toolkit'
import axios from 'axios'

export const fetchPizzas = createAsyncThunk('pizza/fetchPizzasStatus', async (params, thunkAPI) => {
  let { getCategoryId, getRegime, order, getSearchValue, currentPage } = params
  const { data } = await axios.get(`https://62fb49efe4bcaf535180e06c.mockapi.io/items?${getCategoryId}&sortBy=${getRegime}&order=${order}&search=${getSearchValue}&limit=4&page=${currentPage}`)

  if (data.length === 0) {
    return thunkAPI.rejectWithValue(' ')
  }
  return thunkAPI.fulfillWithValue(data)
})

const initialState = {
  items: [],
  status: 'loading' // loading | success | error
}

export const pizzaSlice = createSlice({
  name: 'pizza',
  initialState,
  reducers: {
    setItems(state, action) {
      state.items = action.payload
    },
  },
  extraReducers: {
    [fetchPizzas.pending]: (state) => {
      state.items = []
      state.status = 'loading'
    },
    [fetchPizzas.fulfilled]: (state, action) => {
      state.items = action.payload
      state.status = 'success'
    },
    [fetchPizzas.rejected]: (state) => {
      state.items = []
      state.status = 'error'
    },
  }
})

export const pizzaDataSelector = (state) => state.pizza

export const { setItems } = pizzaSlice.actions

export default pizzaSlice.reducer